html {
    min-height: 100%;
}

body {
    background-image: linear-gradient( -225deg, #FD6C9E 0%, #EB6362 52%, #FE28A2 100% );
    font-family: sans-serif;
    margin: 0;
    min-height: 100%;
    padding: 0;
    padding-bottom: 5rem;
}

h1, h2 {
    color: white;
    font-family: 'Staatliches', cursive;
    letter-spacing: 1px;
    padding-top: 3rem;
    text-align: center; 
    text-transform: uppercase;
}

input[ type="date" ], input[ type="time" ] {
    background-color: #fff;
    border: 1px solid #D1D1D1;
    border-radius: 4px;
    box-shadow: none;
    box-sizing: border-box;
    height: 38px;
    padding: 6px 10px;
}

textarea {
    height: 200px;
    resize: none;
}

.alerta-error {
    color: #990000;
    font-family:  'Staatliches', cursive;
    font-size: 1.5rem; 
    text-align: center;
    text-transform: uppercase;
}

.cita {
    background: white;
    border-bottom: 1px solid #e1e1e1;
    color: #000;
    padding: 2rem;
}
  
.cita p {
    font-weight: bold;
    margin-bottom: .8rem;
}

.cita p span {
    font-weight: normal;
}

.cita:first-of-type {
    border-top-left-radius: 1rem;
    border-top-right-radius: 1rem;
}

.cita:last-of-type {
    border-bottom-left-radius: 1rem;
    border-bottom-right-radius: 1rem;
}

.button-eliminar, .button-eliminar:hover {
    background-color: #c10059;
    color: white;
    margin-top: 2rem;
}

.lista-citas .media {
    border-bottom: 1px solid #e1e1e1;
    padding-bottom: 3rem;
}
  
.lista-citas .media:last-of-type {
    border-bottom: 0;
}

.lista-citas .media-body p {
    margin:0;
}

.lista-citas .media-body button {
    margin-top: 1rem !important;
}

.lista-citas .media-body span {
    font-weight: bold;
}
